    import {uniCalendar} from "@/components/uni-calendar/uni-calendar"
    import uniPopup from "@/components/uni-popup/uni-popup.vue"
    var graceChecker = require("../../js_sdk/graceui-dataChecker/graceChecker.js")
	export default {
        components: {
            uniCalendar,
            uniPopup
        },
		data() {
            const currentDate = this.getDate({
                format: true
            })
			return {
                nowtime: '',
                showForm: false,
                sss: [
                    {
                        id: 0,
                        name: "1"
                    },
                    {
                        id: 1,
                        name: "2"
                    }
                ],
                date: currentDate
			}
		},
		onLoad() {
            let date = new Date
            let Y = date.getFullYear() + '-'
            let M = date.getMonth() + 1 + '-'
            let D = date.getDate();
            let nowtime = Y + M + D
            this.nowtime = String(nowtime)
        },
        computed: {
            startDate() {
                console.log(this.getDate('start'), '11111')
            },
            endDate() {
                console.log(this.getDate('end'), '22222')
                return this.getDate('end');
            }
        },
		methods: {
            // 点击日期触发
			change(e) {
                console.log(e);
            },
            // 更换月份触发
            monthSwitch(e) {
                console.log(e)
            },
            onForm(e) {
                this.showForm = true
                this.$refs.popup.open()
            },
            bindDateChange: function(e) {
                console.log(e.target.value, '1123123')
                this.date = e.target.value
            },
            // 表单确认
            formSubmit(e) {
                let rule = [
                    {name:"picker", checkType: "notnull", checkRule:"",  errorMsg:"请选择日期"},
                    {name:"radio", checkType: "in", checkRule:"radio1,radio2",  errorMsg:"请选择方式"},
                    {name:"input", checkType: "therge",  errorMsg:"请输入金额"}
                ];
                console.log(e, 'eeee')
                console.log(e.detail.value, 'ee')
                let formData = e.detail.value;
                let checkRes = graceChecker.check(formData, rule);
                if(checkRes){
                    uni.showToast({title:"验证通过!", icon:"none"});
                } else {
                    uni.showToast({ title: graceChecker.error, icon: "none" });
                }
            },
            // 表单取消
            formReset(e) {
                this.showForm = false
                this.$refs.popup.close()
            },
            getDate(type) {
                const date = new Date();
                let year = date.getFullYear();
                let month = date.getMonth() + 1;
                let day = date.getDate();
                if (type === 'start') {
                    year = year - 60;
                } else if (type === 'end') {
                    year = year;
                }
                month = month > 9 ? month : '0' + month;;
                day = day > 9 ? day : '0' + day;
                return `${year}-${month}-${day}`;
            }
		}
	}
